body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
html{
  min-height: 100%;
  background-color: #f1f3f6;
}
.container, body{
  background-color: #f1f3f6 !important;
}

.title > h1{
  color: #242f3e;
  position: relative;
  padding-left: 20px;
  font-size: 26px;
  -webkit-flex:0 0 auto;
          flex:0 0 auto;
}
.title>h1::before{
  position: absolute;
  content: '';
  display: inline-block;
  width: 5px;
  left: 0px;
  height: 100%;
  background-color: #e4e6e9;
}
.title{
  margin-top: 30px;
  display: -webkit-flex;
  display: flex;
}
.title__line{
  width: 100%;
  position: relative;
}
.title__line::after{
  content: '';
  position: absolute;
  display: inline-block;
  height: 1px;
  right: 0;
  top: 17px;
  left: 10px;
  background-color: #e4e6e9;
}
.Invoices {
  margin-bottom: 25px; }
  .Invoices__actions, .Invoices__main {
    margin-top: 35px;
    background-color: #fff;
    border: 1px solid #e4e6e9;
    padding: 25px; }
  .Invoices__main .table {
    margin-top: 15px; }
    .Invoices__main .table-head {
      background-color: #f1f3f6;
      min-height: 50px;
      border-radius: 5px 5px 0 0;
      margin: 0;
      -webkit-align-items: center;
              align-items: center;
      color: #778096; }
    .Invoices__main .table-line {
      position: relative;
      margin: 0;
      -webkit-align-items: center;
              align-items: center;
      font-size: 14px;
      color: #898989;
      min-height: 70px; }
      .Invoices__main .table-line.open {
        background-color: #f1f3f6; }
        .Invoices__main .table-line.open .comment {
          -webkit-flex: 1 1 auto;
                  flex: 1 1 auto;
          background-color: #fff;
          height: 100%;
          overflow: visible;
          border: 1px solid #f1f3f6;
          border-radius: 5px; }
      .Invoices__main .table-line .comment {
        height: 65px;
        overflow: hidden;
        width: 100%; }
      .Invoices__main .table-line:hover {
        cursor: pointer;
        background-color: #f1f3f6; }
        .Invoices__main .table-line:hover .delete::after {
          position: absolute;
          content: '\D7';
          right: 1px;
          top: 0px;
          font-size: 26px;
          line-height: 20px;
          color: #e4e6e9; }
        .Invoices__main .table-line:hover .delete:hover {
          cursor: pointer; }
          .Invoices__main .table-line:hover .delete:hover::after {
            color: #242f3e; }

.CreateInvoice form {
  position: relative;
  margin-top: 35px;
  background-color: #fff;
  padding: 15px;
  border: 1px solid #e4e6e9; }
  .CreateInvoice form .form-group {
    padding: 0 25px 25px 25px;
    border: 1px solid #e4e6e9;
    border-radius: 0 15px 0 0; }
    .CreateInvoice form .form-group label {
      margin-top: 25px;
      font-weight: 500;
      font-size: 14px; }
  .CreateInvoice form a {
    height: 100%; }
  .CreateInvoice form .exit::after {
    position: absolute;
    content: '\D7';
    right: 1px;
    top: 0px;
    font-size: 26px;
    line-height: 20px;
    color: #242f3e; }
  .CreateInvoice form .exit:hover {
    cursor: pointer; }
    .CreateInvoice form .exit:hover::after {
      color: #e4e6e9; }

